import React from "react";
import "./Person.css";

const Person = (props) => {
  return (
    <div className="mycard">
      <p onClick={props.click}>
        My name is {props.name} and I am {props.age} years old.
      </p>
      <input type="text" value={props.name} onChange={props.change} />
    </div>
  );
};

export default Person;
