import React, { Component } from "react";
import Person from "./Person/Person";
// import People from "./People";

class App extends Component {
  state = {
    persons: [
      { name: "Ali", age: 29 },
      { name: "Reza", age: 32 },
      { name: "Hamid", age: 36 },
    ],
  };

  changeNameHandler = (newName) => {
    this.setState({
      persons: [
        { name: newName, age: 29 },
        { name: "Reza", age: 32 },
        { name: "Hamid", age: 36 },
      ],
    });
  };

  updateNameHandler = (event) => {
    this.setState({
      persons: [
        { name: "Ali", age: 29 },
        { name: event.target.value, age: 32 },
        { name: "Hamid", age: 36 },
      ],
    });
  };

  render() {
    const myStyle = {
      marginLeft: "auto",
      marginRight: "auto",
      marginTop: "30px",
      marginBottom: "20px",
      width: "20%",
      display: "block",
    };
    return (
      <div>
        <button
          type="button"
          style={myStyle}
          onClick={this.changeNameHandler.bind(this, "Sara")}
        >
          Click Me
        </button>
        <Person
          name={this.state.persons[0].name}
          age={this.state.persons[0].age}
        />
        <Person
          name={this.state.persons[1].name}
          age={this.state.persons[1].age}
          click={this.changeNameHandler.bind(this, "Niloofar")}
          change={this.updateNameHandler}
        />
        <Person
          name={this.state.persons[2].name}
          age={this.state.persons[2].age}
        />
        {/* <People /> */}
      </div>
    );
  }
}

export default App;
